<template>
  <div class="hos-offices">
    <el-select class="offices-input" v-model="dept1" placeholder="选择一级科室" :style="seletStyle">
      <el-option v-for="item in departmentList" :key="item.id" :label="item.dValue" :value="item.dKey" />
    </el-select>
    <el-select class="offices-input" v-model="dept2" placeholder="选择二级科室" :style="seletStyle">
      <el-option v-for="item in subDepart" :key="item.id" :label="item.dValue" :value="item.dKey" />
    </el-select>
  </div>
</template>

<script>
// 二级联动
import { departmentList, subDepartmentList } from "data/listBody"
import { areaProvince } from "../data/address";
import queryList from "utils/queryList";
let changePower = true;

export default {
  name: "hosOffices",
  props:['seletStyle','defaultValue'],
  data() {
    return {
      departmentList,   
      subDepart: [], //二级科室
      dept1: "",
      dept2: "",
    };
  },
  methods: {
    handleData(num) {
      //处理二级科室
      if( !num ) return;
      this.subDepart = this.lodash.filter(subDepartmentList, (item) => {
        return item.parentKey === num
      })
    },
    getData(){
      let params = {
        dept1: this.dept1,
        dept2: this.dept2
      }
      return params;
    }
  },
  watch: {
    defaultValue(){
      if( this.defaultValue ) {
        const { dept1, dept2 } = this.defaultValue;
        changePower = false;

        // 一级科室
        if( !dept1 ) return changePower = true;
        let dept1Array = this.lodash.filter(departmentList, item=>{
          return item.dValue == dept1
        })
        this.dept1 = dept1Array[0].id;

        // 二级科室
        if( !dept2 ) return changePower = true;
        let dept2Array = this.lodash.filter(subDepartmentList, item=>{
          return item.dValue == dept2
        })
        this.dept2 = dept2Array[0].id;
        changePower = true;
      }
    },
    dept1: function(curVal, oldVal) {
      if( !curVal || !changePower ) return;
      this.subDepart = [];
      this.dept2 = "";
      this.handleData(curVal);
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./../assets/less/color.less");
.hos-offices {
  display: flex;
  .offices-input{
    margin-right: 20px;
    width: 210px;
    &.last-child{
      margin-right: 0;
    }
  }
}
</style>