<template>
  <el-container v-loading="loading">
    <div class="box">
      <h3>{{activeheader}}</h3>
      <div class="input-box">
        <span></span>
        <label for class="label require">医生名称：</label>
        <el-input class="input-width" v-model="name" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label class="label require">所在地区:</label>
        <user-address ref="addDocAddress" :defaultValue="defaultAddress" seletStyle="width:216px"/>
      </div>
      <div class="input-box">
        <label class="label require">所在医院:</label>
        <el-select v-model="hospitalId" placeholder="请选择">
          <el-option
            v-for="item in hosName"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="input-box">
        <span></span>
        <label for class="label">所在科室：</label>
        <el-input class="input-width" v-model="dept" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label class="label require">标准科室:</label>
        <hos-offices ref="addDocOffices" :defaultValue="defaultOffices" seletStyle="width:216px"/>
      </div>
      <div class="input-box">
        <label class="label require">医生职称:</label>
        <el-select v-model="level" placeholder="请选择">
          <el-option
            v-for="item in doctorTitleList"
            :key="item.id"
            :label="item.dValue"
            :value="item.dKey"
          ></el-option>
        </el-select>
      </div>
      <div class="input-box">
        <span></span>
        <label for class="label require">医生电话：</label>
        <el-input class="input-width" v-model="phone" placeholder="请输入内容"></el-input>
      </div>
      <div class="input-box">
        <label for class="label">上传照片</label>
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
        >
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>
      <div class="input-box">
        <label class="label">医生简介：</label>
        <el-input type="textarea" :rows="2" size="medium" placeholder="请输入内容" v-model="info"></el-input>
      </div>
      <div class="input-box">
        <label class="label">医生擅长：</label>
        <el-input type="textarea" :rows="2" size="medium" placeholder="请输入内容" v-model="skill"></el-input>
      </div>
      <div class="input-box">
        <label class="label">备注：</label>
        <el-input type="textarea" :rows="2" size="medium" placeholder="请输入内容" v-model="remark"></el-input>
      </div>
      <div class="input-box box-last">
        <el-button type="primary" @click="submit">提交</el-button>
        <el-button @click="cancle">取消</el-button>
      </div>
    </div>
  </el-container>
</template>

<script>
import { doctorTitleList } from "data/listBody";
import check from "utils/check";
import UserAddress from "components/address";
import HosOffices from "components/hosOffices";

let headers = ['新增医生','修改医生信息'];
let requireArray = ["name", "hospitalId", "dept1", "dept2", "level", "province", "city", "country", "phone"]; //必传字段

export default {
  name: "addDoc",
  data() {
    return {
      name: "",
      dept: "",
      hospitalId: "",
      level: "",
      imageUrl: "",
      phone: "",
      info: "",
      skill: "",
      remark: "",
      loading: false,
      id: '',
      defaultAddress: null, //默认地址
      defaultOffices: null, //默认科室
      activeheader: headers[0],
      check: check.methods.check,
      checkTip: check.tips.checkTip,
      doctorTitleList,
      hosName: [
        { value: "01", label: "xxx-p医院" },
        { value: "02", label: "---xx医院" },
        { value: "03", label: "其他" }
      ]
    };
  },
  components: {
    UserAddress,
    HosOffices
  },
  mounted(){
    this.editInit();
  },
  methods: {
    editInit(){
      let { type, id } = this.$route.query;
      if( type === 'edit' ) this.activeheader = headers[1];
      if( !id ) return;
      this.loading = true;
      this.axios.get(`/doctor/${id}`).then((res) => { //查询产品详情
        if(res.data.code === this.ok){
          this.dataInit(res.data.data)
          setTimeout(() => {
            this.loading = false;
          }, 20);
        }
      }).catch((err) => {
        console.log(err)
        this.loading = false;
        this.$message(err.data.message)
      })
    },
    dataInit(data){
      data = data || {};
      let { name, hospitalName, dept, dept1, dept2, headIcon, hospitalId, level, phone, skill, remark, info, id, province, city, country } = data;
      this.name = name;
      this.hospitalName = hospitalName;
      this.dept = dept;
      this.hospitalId = hospitalId;
      this.level = level;
      this.phone = phone;
      this.skill = skill;
      this.remark = remark;
      this.info = info;
      this.id = id;
      this.imageUrl = headIcon;
      this.defaultAddress = {
        province,
        city,
        country
      }
      this.defaultOffices = {
        dept1,
        dept2
      }
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    submit() {
      let addressParams = this.$refs.addDocAddress.getData();
      let deptParams = this.$refs.addDocOffices.getData();
      let params = {
        name: this.name,
        dept: this.dept,
        hospitalId: this.hospitalId,
        level: this.level,
        phone: this.phone,
        info: this.info,
        remark: this.remark,
        skill: this.skill
      };
      let param = this.lodash.assign({}, params, addressParams, deptParams);
      this.check(param, requireArray).then(()=>{
        this.addUser(param) 
      },()=>{ 
        this.$message(this.checkTip) 
      })
      .catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    editUser(param){ //修改用户
      param.id = this.id;
      this.axios.put('/doctor',param).then((res)=>{
        if(res.data.code === this.ok){
          this.$router.go(-1);
        }
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    addUser(param) { //添加用户
      if(this.$route.query.type === 'edit') return this.editUser(param);
      this.axios.post("/doctor", param).then(res => {
        if (res.data.code === this.ok) {
          this.$router.go(-1);
        }
      })
      .catch(err => {
        console.log(err);
        this.$message(err.data.message);
      });
    },
    cancle() {
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
.box {
  padding-left: 10px;
}
.input-box {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.label {
  width: 100px;
  min-width: 100px;
  margin-left: 20px;
  &.require {
    position: relative;
    &::before {
      content: "*";
      color: red;
      position: absolute;
      left: -10px;
      top: 4px;
    }
  }
}
.label-left {
  margin-left: 15px;
}
.input-width {
  width: 216px;
}
.el-textarea {
  width: 300px;
}
.el-select {
  margin-right: 10px;
  width: 216px;
}
.box-last {
  margin-top: 20px;
}
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.box-last {
  margin-left: 20px;
}
</style>
 